.albumContent {
  padding-top: 0px;
}
.albumContainer {
  position: relative;
  width: 50%;
 } 
.albumReal {
  opacity: 1;
  display: block;
  width: 300px;
  height: auto;
  transition: 0.5s ease-out;
  backface-visibility: hidden;
}
.albumBlur {
  filter: blur(20px);
  opacity: 1;
  display: block;
  width: 300px;
  height: auto;
  transition: none;
  backface-visibility: hidden;
}
.albumMiddle {
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  width: 300px;
  top: 0;
  left: 0;
  text-align: center;
} 
.albumContainer:hover .albumReal {
  opacity: 0.3;
} 
.albumContainer:hover .albumMiddle {
  opacity: 0.8;
}
.albumText {
  background-color: #04AA6D;
  color: rgb(240, 231, 231);
  font-size: 16px;
  padding: 32px 32px;
}
@media screen and (max-width: 500px){
  .albumContent {
    padding-top: 66.9px;
  }
}